.staffing {
    /* height: 100%; */
}

.page-header {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url(../../assets/images/Recruitment_3.jpg);
    background-size: cover;
    background-position: 50% 20%;

    z-index: 10;
    width: 100%;
    height: 400px;
    margin: 0;
}

.header-title {
    position: relative;
    z-index: 11;
    margin: 10px 0 0;

    color: #ffffff;
    font-family: Prata;
    font-size: 49px;
    font-weight: 400
}

.page-header::after {
    content: "";

    background-color: rgb(0, 0, 0, .5);
    position: absolute;
    top: 0;
    left: 0;

    height: 100%;
    width: 100%;
    
}

.text-area {
    height: auto;
    margin: 2.5% 10%;
}

.ta-left {
    text-align: left;
}

.ta-justify {
    text-align: justify;
}

.letter-spacing {
    letter-spacing: 2.5px;
}

.staffing-text {
    position: relative;
    z-index: 10;
    width: 100%;
    margin: 0;

    font-size: 14px;
    text-align: justify;

    white-space: pre-wrap;
}

.consulting-list {
    width: 75%;
}

.consulting-list-item {
    margin: 10px auto;
}

.consulting-margin {
    margin: 25px auto;
}