footer {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;    
    height: 150px;
    width: 100%;
    /* background-color: hsl(189, 14%, 81%); */
    font-family: 'Thasadith';
    background-color: #AABCBF;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 0px 5px 0px;
}

.footer-links {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.footer-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    
    padding: 0 0 0 15px;
    margin: 10px 5%;
    /* height: 50px; */
    width: max-content;

    color: black;
    overflow: hidden;   
}

.footer-row > li{
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;

    list-style: none;

    height: 20px;
    width: fit-content;

    border-right: 1px solid black;
}

.footer-link {
    margin: 0;
    padding: 5px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer-contact-text {
    margin: 0;
    padding: 5px ;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 14px;

    /* border-right: 1px solid gray; */
}

.link {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.gatsby-link {
    font-weight: 300;
    text-decoration: none;
    color: black;
}

.social-media-links {
    margin-right: 50px;
}